<template>
  <div class="contanier">
    <div class="row" v-if="showStatment" >
      <InventoryBalance-Table :StockId="StockId.id" :FromDate="FromDate" :ToDate="ToDate" class="flex md12 sm12 xs12"/>
    </div>
    <div class="row" v-if="!showStatment" style="justify-content: center;" >
      <va-card :title="$t('tables.labelInventoryBalance')" class="flex md8 sm12 xs12">
        <form @submit.prevent="okCreatefun()">
          <div class="row flex-center">
            <div class="flex md6 sm12 xs12 px-1">
              <va-date-picker
                :label="$t('forms.dateTimePicker.from')"
                v-model="FromDate"
              />
            </div>
            <div class="flex md6 sm12 xs12 px-1">
              <va-date-picker
                :label="$t('forms.dateTimePicker.to')"
                v-model="ToDate"
              />
            </div>
            <div class="flex md4 sm12 xs12 px-1">
              <va-select
                :label="$t('placeholder.StockName')"
                v-model="fKStocsk.value"
                required
                searchable
                textBy="name"
                keyBy="id"
                :options="fKStocsk.options"
              />
            </div>
          </div>

          <div class="d-flex justify--center mt-3">
            <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t('modal.ok') }} </va-button>
          </div>
        </form>
      </va-card>
    </div>
  </div>
</template>

<script>
import InventoryBalanceTable from './InventoryBalance/InventoryBalanceTable'
import StockService from '../../../services/BasicData/StockService'

export default {
  name: 'InventoryBalance',
  components: {
    InventoryBalanceTable,
  },
  data () {
    return {
      fKStocsk: {
        value: '',
        options: [],
      },
      StockId: 0,
      FromDate: '',
      ToDate: '',
      showStatment: false,
      stopBtn: false,
    }
  },
  created () {
    this.getCustomer()
  },
  methods: {
    getCustomer () {
      StockService.getAll()
        .then(response => {
          this.fKStocsk.options = response.data
        },
        err => {
          console.error(err)
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    okCreatefun () {
      this.stopBtn = true
      if ((this.fKStocsk.value !== null && this.fKStocsk.value !== undefined && this.fKStocsk.value !== '')) {
        this.StockId = this.fKStocsk.value

        this.showStatment = !this.showStatment
      } else {
        this.stopBtn = false
        this.showToast(this.$t('generic.errorChooseStock'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      }
    },
  },
}

</script>

<style lang="scss">
  .row-equal .flex {
    .va-card {
      height: 100%;
    }
  }

  .dashboard {
    .va-card {
      margin-bottom: 0 !important;
    }
  }

</style>
