<template>
  <va-card :title="$t('tables.labelInventoryBalance')" class="w-100">

    <va-data-table
      :fields="fields"
      :data="dataTable"
      :loading="loading"
      :totalPages="totalPages"
      @page-selected="readItems"
      api-mode
    >
      <template slot="marker" slot-scope="props">
        {{props.rowIndex + 1}}
      </template>
      <template slot="stock" slot-scope="props">
        <va-collapse withBackground>
          <span slot="header"> {{props.rowData.products +'(' + props.rowData.balance.toString()+')' }} </span>
          <div slot="body">
            <div v-for="(product, j)  in props.rowData.uints" :key="j">
              <va-checkbox  v-model="cheacks[props.rowIndex][j]" @input="select(props.rowIndex, j)"
                :label="product"
              />
            </div>
          </div>
        </va-collapse>

      </template>
    </va-data-table>

  </va-card>
</template>

<script>
import InventoryBalanceService from '../../../../services/Reports/InventoryBalanceService'
export default {
  props: {
    StockId: Number,
    FromDate: String,
    ToDate: String,
  },
  data () {
    return {
      modelTitle: '',
      modelOktext: '',
      perPage: 10,
      totalPages: 0,
      search: {},
      dataTable: [],
      loading: true,
      currentobject: {},
      cheacks: [],
    }
  },

  computed: {
    fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',

      }, {
        name: '__slot:stock',
        title: this.$t('tables.headings.name'),
      }]
    },
  },
  watch: {
    dataTable: function (val) {
      this.totalCredit = 0.0
      this.totalDepit = 0.0
      this.total = 0.0
      this.dataTable.forEach(element => {
        this.totalCredit = parseFloat(this.totalCredit + element.sumCredit)
        this.totalDepit = parseFloat(this.totalDepit + element.sumDepit)
        this.total = Math.abs(parseFloat(this.totalDepit - this.totalCredit))
      })
    },
  },
  created () {
    this.readItems()
  },
  methods: {
    /*  api Padination */
    readItems (page = 1) {
      this.loading = true
      const params = {
        PageSize: this.perPage,
        PageNumber: page,
        Stock_Id: this.StockId === 0 ? null : this.StockId,

        // eslint-disable-next-line eqeqeq
        DateFrom: this.FromDate == '' ? null : this.FromDate,
        // eslint-disable-next-line eqeqeq
        DateTo: this.ToDate == '' ? null : this.ToDate,
        Company_Activity: 1,

      }
      InventoryBalanceService.getAllPagnation(params)
        .then(response => {
          console.log(response)
          this.dataTable = response.data
          var pagination = JSON.parse(response.headers['x-pagination'])
          this.totalPages = pagination.TotalPages
          this.loading = false
          this.chackablelist()
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    chackablelist () {
      this.dataTable.map((e) => {
        var ch = []

        try {
          e.balance = e.balance.replace(/'/ig, '')
          // eslint-disable-next-line eqeqeq
          if (e.balance != '') {
            e.uints = e.balance.split(', ')
            for (let index = 0; index < e.uints.length; index++) {
              ch.push(false)
            }
            this.cheacks.push(ch)
            if (e.uints.length === 1) {
              e.balance = e.balance.replace(/,/ig, '')
            }
          } else {
            e.uints = []
            ch = []
            this.cheacks.push(ch)
          }
        } catch (error) {
          console.error(error)
        }
      })
    },
    select (parentindex, index) {
      console.log(this.cheacks[parentindex][index])
    },

  },
}

</script>

<style lang="scss">
  i.fa.fa-exclamation {
    color: red !important;
  }

</style>
